function Home() {

    const scriptURL = 'https://script.google.com/macros/s/AKfycbwx6EJbqC6U8MMaYC5vnfShpCEEnBR_gvx1WbEBhAU-PzqjPTP0Mg2cAwM8wWdS_o3_kQ/exec'
    
    const form = document.getElementById("preRegister")
    const [disable, setDisable] = useState(false);
    const [email, setEmail] = useState('');
    const [status, setStatus] = useState('');
    
    const handleSubmit = e => {
        e.preventDefault();
        setDisable(true)  
        fetch(scriptURL, { method: 'POST', body: new FormData(form)})
            .then(response => {
                  console.log('Success!', response);
                  setStatus({ type: 'success' });
                  setDisable(false);
                  setEmail('');
                  })
              .catch(error => {
                  setDisable(false);
                  setStatus({ type: 'error', error });
              })  
        }
    
       
    return (

        
        <home.Wrapper>

                <home.Container>
              
              
                    <home.Info>
                        <p>
                        We at CryptoScholars have been listening to the community. After recent events in the crypto space with respect to impropriety, we felt it was prudent to update our approach to giving back to the community moving forward. <br></br>

Our initial proposal was to have an NFT sale, to bootstrap a prize fund. This prize fund would, ideally, attract many new players who would compete via small entry fees. These entry fees would be the primary vehicle for the prize fund being replenished. Once a year, we’d hold a high school only chess tournament for high school students to compete for a scholarship. <br/>

After 6 months of speaking with the crypto community, there is one conclusion. There simply isn’t demand for a chess tournament in the crypto space at this time. As of January 23rd, 2022, we had 900+ followers on Twitter, and 118 people in our discord. We made an announcement to hold an open tournament in our discord with 118 members present. 1 person signed up. 
If we held an actual tournament for $10000, with two people competing, that would be the first and last tournament.
<br/>

we want to be as transparent as possible about our new roadmap moving forward. 
The tournament is gone. The chess game is gone. 

You are buying these NFTs strictly as JPEGs you want to own.
We make no promises about the value of the NFTs appreciating in value. To us, they have no value beyond being pieces of art. 
<br/>

One thing our founder kept hearing in twitter spaces was “we love the idea of giving back, but we don’t play chess.” Or “We don’t play chess, but we love the idea of giving back.” 

If the crypto chess space does expand in popularity in the future, we can revisit the idea then. 
Thank you for believing in our mission.
<br/>
                        </p>
                    </home.Info>   
               
               

                        </home.Container>      
    </home.Wrapper>
          
    )
}



