import React, { useState } from 'react';
import Offcanvas from 'react-bootstrap/Offcanvas'
import MenuIcon from '@material-ui/icons/Menu';
import { Link } from "react-router-dom"
import styled from "styled-components";




function OffcanvasNav() {

    const [show, setShow] = useState(false);
    
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
    <>
        <Canvas.button onClick={handleShow} className="menu" >
            <MenuIcon  className="MenuIcon" fontSize="large"/>
        </Canvas.button>
        
        <Offcanvas className="offcanvas"  show={show} placement='end' backdrop= {true}  scroll= {true} onHide={handleClose}>
        <Offcanvas.Header closeButton closeVariant='white'>
          <Offcanvas.Title>CryptoScholars</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
        <Link  id="nava" className="homenav" to="/home"  onClick={handleClose} >Home</Link>
        </Offcanvas.Body>
        <Offcanvas.Body className="Nav_Menu">
        <Link  id="nava" className="newsnav" to="/news" onClick={handleClose} >news</Link>
        </Offcanvas.Body>

     
      </Offcanvas>
    </>
    )
}
const Canvas = {
  button: styled.button`
color: white;
border: none;
background: transparent; 
     
    }
  `
};

export default OffcanvasNav